export function SubHeading1(props){
  return(<div style={{paddingTop:"10px",paddingBottom:"10px"}}>
    <div style={{color:"orange",background:"black",padding:"5px",textAlign:"center",fontSize:"15px"}}>{props.SubHeading}</div>
  </div>)
}

export function SubHeading2(props){
  return(
    <div style={{paddingTop:"20px",paddingBottom:"0px"}}>
    <div style={{color:"orange",background:"black",padding:"3px",paddingTop:"8px",fontSize:"14px",textAlign:"left",borderTopLeftRadius:"10px"}}>{props.SubHeading}</div>
  </div>
  )
}


