export function Client16s(){
  
  function Code(){
    return (<div >

<AdoniaReporterProfile/>
<p></p>
<div style={{color:"red"}}>Discover the mystery behind our mighty halls of residence. by Kizza Adonia a.k.a Freedom fighter</div><p></p>
Makerere university has nine halls of residence at the main campus; of which six are for males and the remaining three are for females (girls).
<p></p>


And today, as we agreed, we're moving to the goat land.<p></p>

<div style={{color:"orange",background:"black",padding:"5px",textAlign:"center",fontSize:"15px"}}>University Hall</div>

University Hall is a service oriented unit for students and staff. It was opened as a hall of residence in July 1957. It is among the oldest halls on campus.

The first intake into this Hall was second year students who were <span style={{color:"red"}}>transferred from (Northcote hall by then)</span> now Nsibirwa hall. The structure of the building was rectangular in form. During 1960/61 the original plan was alterated.
<p></p>
 <div style={{color:"red"}}>HALL CULTURE</div>
The students call their hall, the only hall on campus, with a culture of special and strong meekness symbolised by their association <span style={{color:"red"}}>with a goat</span> - thus the language of goat land.
 "Heb! Heb! Heb!"
<br></br>
The monument of a goat represents the hall and is highly respected by both the former and current students who reside in the hall.

In 2004, the <span style={{color:"red"}}>goats decided to look for she-goats</span>, the hunting ended up in a hostel called Garden courts which is found off Sir Apollo Kagwa road. 
<br></br>
They united both cultures and came up with a new name <span style={{color:"red"}}>"Unicourts solidarity"</span>.

Since that day, all cultural activities are are carried out together as "Unicourt solidarity"
<p></p>
<AdoniaReporterProfile/>
<p></p>



<div style={{color:"orange",background:"black",padding:"5px",textAlign:"center",fontSize:"15px"}}>Lumumba Hall</div>

Lumumba hall was built in 1960's and was opened in 1971. It was named after a Congolese freedom fighter, <span style={{color:"red"}}>Patrice Lumumba</span>.

It is the biggest hall on campus, It is located on Lumumba road just next to Mary Stuart. The hall accommodates about 752 students, with 241 double rooms and two open blocks.
<p></p>
 <div style={{color:"red"}}>HALL CULTURE</div>
The Hall logo is an elephant and the students call themselves elephants. The cultural symbol is Gongom. Gongom is addressed as His Majesty, highness and dressed in <span style={{color:"red"}}>attire (a graduate gown).</span>
<p></p>
The Gongom monument was introduced by former students including the <span style={{color:"red"}}>late Gen. Elly Tumwine</span>. The Gongom monument has a protection brigade known as Gongom protection Brigade of <span style={{color:"red"}}>10 members manned by a general</span>. 

It is affiliated to Mary Stuart Hall of female students. This affiliation brings social, cultural and political solidarity known as LUMBOX.
<p></p>
However on September 12 2022, in a letter addressed to the Dean of students, the university <span style={{color:"red"}}>vice-chancellor, Prof. Barnabas Nawangwe ordered for the closure</span> of the Hall to pave way for renovations due to poor conditions at the moment. Students were ordered to relocate to other halls of residence where they are upto now as the hall is under 
renovation.
<p></p>
<AdoniaReporterProfile/>
    <p></p>
    <div style={{color:"orange",background:"black",padding:"5px",textAlign:"center",fontSize:"15px"}}>Makerere University</div>

Established in 1922, as a humble technical school, Makerere is one of the oldest and most prestigious English universities in Africa.

In January of that year, the school which was later renamed Uganda technical college opened it's doors to 14 students who began studying carpentry, building and mechanics.
<p></p>
The college soon began offering various other courses in medical care, Agriculture, Veterinary science and teacher training. It expanded over the years to become a center for higher education in East Africa in 1935. In 1937, the college started developing into an institution of higher education, offering post-school certificate courses.

In 1949, it became a university college affiliated to the university college of London offering courses leading to the general degrees of it's then mother institution.

<p></p>With the establishment of the university of East Africa in June 29, 1963, the special relationship with the university of London came to a close and the degrees of the university of East Africa were instituted.

On July, 1970, makerere became an independent national university of the republic of Uganda offering undergraduate and postgraduate courses leading to it's own awards.

Makerere university offers not only day but also evening and external study programmes to students body of about 40,000 undergraduates and about 3,500 postgraduates (both Ugandan and foreign). It is also very active centre for research.

The university transitioned from a faculty based to collegiate system on 1st July 2011, and as of Friday 30th Dec 2011, makerere university officially transformed into a collegiate university with 9 constituent colleges and as at 1st July 2014, 10 constituent colleges including the school of Law all operating as autonomous units of the university.

<p></p><span style={{color:"red"}}>On Thursday Oct 6, 2022</span>, makerere university celebrated it's 100 year anniversary as one of the most recognised and prestigious institutions for higher education in Africa, and Norway has been a longstanding partner of the university since 1960's.

This information is for the purposes of understanding the power behind the hill of endless opportunities.
<br></br>
<p></p>
<AdoniaReporterProfile/>
<p></p>
<div style={{color:"orange",background:"black",padding:"5px",textAlign:"center",fontSize:"15px"}}>Who is Kizza Adonia a.k.a Freedom Fighter?</div>

Read details about this gentleman at the bottom of his photo below:
<p></p>
<img src={client16simg1} class=" d-block w-100" alt="Kayas loves you"  />
  Kizza Adonia is a year two student in Makerere University residing in University hall and persuing a Bachelor of Arts with Education under the College of Education and External Studies



 </div>)
  }
  
  return(
    <div>
      <MukNav/>
      <Client id="client16s" date="20th Dec, 2023" seeMore="/pages/campus" opinionsHeading1="reactions have been posted in reaction to Saviour's statement on those who shout the loudest weewee." opinionsHeading2="reactions have been posted in reaction to his information. Scroll under this form to see." opinionsHeading3="reactions below:" code={Code()} headline1="Makerere University Histories compiled by Kizza Adonia a.k.a Freedom Fighter"  formHeading="Post your reaction" formButtonLabel="Post"  opinionsHeading="views below:" showCustomerContact="off" bottomAnchors={<MukAnchors/>} />
    </div>
  )
}

